<template>
  <div id="top-header">
    <div class="header-left-decoration">
      <img src="../assets/logo.svg" class="logo" />
    </div>
    <dv-decoration-5 class="header-center-decoration" />
    <div class="header-right-decoration currentTime">{{ currentTime }}，晴，20℃</div>
    <div class="center-title">{{title}}</div>
  </div>
</template>

<script>
export default {
  name: "TopHeader",
  props:['title'],
  data() {
    return {
      currentTime: "",
    };
  },
  methods: {
    getCurrentTime() {
      var date = new Date();

      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();

      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();

      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }

      if (day >= 0 && day <= 9) {
        day = "0" + day;
      }

      if (hours >= 0 && hours <= 9) {
        hours = "0" + hours;
      }

      if (minutes >= 0 && minutes <= 9) {
        minutes = "0" + minutes;
      }

      if (seconds >= 0 && seconds <= 9) {
        seconds = "0" + seconds;
      }

      this.currentTime =
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds;
    },
  },
  mounted() {
    this.getCurrentTime();
    setInterval(() => {
      this.getCurrentTime();
    }, 1000);
  },
};
</script>

<style lang="less">
#top-header {
  position: relative;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;

  .logo {
    width: 220px;
    height: auto;
    margin-top: 20px;
    margin-left: 20px;
  }

  .header-center-decoration {
    width: 40%;
    height: 60px;
    margin-top: 30px;
  }

  .header-left-decoration,
  .header-right-decoration {
    width: 25%;
    height: 60px;
  }

  .center-title {
    position: absolute;
    font-size: 30px;
    font-weight: bold;
    left: 50%;
    top: 15px;
    transform: translateX(-50%);
  }
}
.currentTime{
  font-size: 20px;
  text-align: right;
  margin-right: 20px;
  margin-top: 30px;
}
</style>
